@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&family=Playfair+Display:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;500;700&family=Open+Sans:wght@700&family=Playfair+Display:wght@400;700&display=swap");
// "https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;800&family=Noto+Sans:wght@100;200;500;700&family=Open+Sans:wght@700&family=Playfair+Display:wght@400;700&display=swap"

html {
  font-size: 100%; /*16px*/
  font-family: "Open Sans", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
a {
  font-family: "Noto Sans";
}
h1 {
  font-size: 3.052rem;
}
h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

p {
  font-size: 1rem;
  font-weight: 200;
}

.header h1 {
  font-weight: bold;
}

.footer {
  padding-top: 30px;
  h1 {
    font-family: "Playfair Display", serif;
    font-size: 24;
  }
}
